define('../src/sublayouts/m31-search/m31-search',['jquery', 'slick'], function($) {

    'use strict';

    var M31_SEARCH = function($component) {

        this.$component = $component;

        return this;
    };

    /**
     * Initialise component
     */

    M31_SEARCH.prototype.init = function() {
        var $this = this;
        this.$html = $('html');
        this.$isRTL = (this.$html.attr('dir') == 'rtl');
        this.shareOpen = false;

        this.$component.off('keyup.m31').on('keyup.m31', function(event) {
            if (event.keyCode == 27) {
                $(this).find('.m12-search__panel--close').click();
            }
        })

        $('.m31-search__item, .m31-search__link').on('click', function(e) {
            e.stopPropagation();
        });

        $('.m31-search__button').off('click.m31search').on('click.m31search', function(e) {
            e.stopPropagation();
            e.preventDefault();

            if ($(window).outerWidth(true) > 1024) {
                $this.$panel = $(this).closest('.m31-search').find('.m31-search__panel');
                var p = $(this).offset().top,
                    ph = $this.$panel.outerHeight(),
                    wh = $(window).height(),
                    _this = this,
                    st = $(window).scrollTop();

                if ((((p - st) + ph) >= wh) && (p - st) >= ph) {
                    $this.$panel.addClass('m31-search__panel--top');
                } else {
                    $this.$panel.removeClass('m31-search__panel--top');
                }
                if ($this.$panel.attr('aria-expanded') == 'true') {
                    $this.hideSharePanel();
                } else {
                    $this.showSharePanel();
                }
                $('.m12-search__panel--close').attr('tabindex', '0');
                $('.m12-search__panel--close, .gray-bg').on('click', function(event) {
                    $this.hideSharePanel();
                    $('body').removeClass('unscrollable');
                });
                $('.m12-search__panel--close').on('keyup', function (event) {
                    if (event.keyCode == 13) {
                        $(this).click();
                    }
                })
                $('body').addClass('unscrollable');
            } else {
                if (!$('.m12-menu_expandable--mobile .m12-menu_bar--wrapper').hasClass('active')) {
                    $('.m12-menu_expandable--mobile').click();
                }
                $(window).trigger('searchOpen')
            }
        });

        //event listener for 'all'


    };

    M31_SEARCH.prototype.hideSharePanel = function() {
        try {
            this.$panel.removeClass('m31-search__panel--visible');
            this.$panel.attr('aria-expanded', false);
        } catch (e) {}
    };

    M31_SEARCH.prototype.showSharePanel = function() {
        this.$panel.addClass('m31-search__panel--visible');
        this.$panel.attr('aria-expanded', true);
        this.$panel.fadeIn();
        // setTimeout(function() {
        //     $('.m12-search__input').focus();
        // }, 250);
    };

    return M31_SEARCH;
});

