						elec = eval( elec ) - commercialData.baseelec;
						subgrelec = commercialData.baseelec * elec2k;
						document.getElementById( "ctl00_ContentPlaceHolder1_fgrelec" ).innerHTML = subgrelec.toFixed( 2 );

